import React from 'react'
import {auth} from "../../firebase"
import CommentContainer from './CommentContainer'

class Post extends React.Component {
    constructor(props) {
        super(props)
        this.handleRemovePost = this.handleRemovePost.bind(this);
        this.handleToggleComments = this.handleToggleComments.bind(this);
        this.state = {
            removed: false,
            comments: false
        }
    }

    async handleRemovePost() {
        let options = {
            method: 'DELETE',
            headers: {
                'auth': auth.currentUser.uid,
                'uid': this.props.uid
            }
        }
        const url = `https://us-central1-capconcept-72a90.cloudfunctions.net/adminApi/hidePost/${this.props.id}`
        await fetch(url, options).then(() => {
            this.setState({
                removed: true
            })
        })
    }

    handleToggleComments() {
        this.setState({
            comments: !this.state.comments
        })
    }



    render() {
        let comments;
        if(this.state.comments) {
            comments = <CommentContainer id={this.props.id} />
        } else {
            comments = <div>notLoaded</div>
        }

        return (
            this.state.removed ?
            <div>
                <h2>Post Removed</h2>
            </div>
            :
            <div className='postFrame'>
                <div className='post'>
                    <div className='userHbox'>
                        <img src='data:image/png;base64,' width='50' height='50' />
                        <h3>{this.props.displayName}</h3>
                        <p>{this.props.uid}</p>
                    </div>
                    <p>{this.props.content}</p>
                    <div className='userHbox'>
                        <button onClick={this.handleToggleComments}>View Comments</button>
                        <button onClick={this.handleRemovePost}>Remove Post</button>
                    </div>
                </div>
                {comments}
            </div>
        )
    }
}

export default Post
